import React from 'react'
import { FiCheck, FiStar } from "react-icons/fi";

const AdvancePricingOne = () => {
    return (
        <div className="advance-pricing">
            <div className="inner">
                <div className="row row--0">
                    <div className="col-lg-6">
                        <div className="pricing-left">
                            <h3 className="main-title">10X your launch</h3>
                            <p className="description">From launch videos to your ads assets. We can cover all of your marketing efforts</p>
                            <a href="/contact">
                                <div className="pricing-btn-group">
                                    <button className="btn-default">Talk to sales</button>
                                </div>
                            </a>
                            <div className="rating">
                                <a href="#rating"><FiStar /></a>
                                <a href="#rating"><FiStar /></a>
                                <a href="#rating"><FiStar /></a>
                                <a href="#rating"><FiStar /></a>
                                <a href="#rating"><FiStar /></a>
                            </div>
                            <span className="subtitle">Rated 4.5/5 stars in 100+ reviews</span>
                        </div>
                    </div>
                    <div className="col-lg-6">
                        <div className="pricing-right">
                            <div className="pricing-offer">
                                <div className="single-list">
                                    <h4 className="price-title">Basic plans include</h4>
                                    <ul className="plan-offer-list">
                                        <li><FiCheck /> Product launch</li>
                                        <li><FiCheck /> Features launch</li>
                                        <li><FiCheck /> Launch advice</li>
                                        <li><FiCheck /> Ads assets</li>
                                    </ul>
                                </div>
                                <div className="single-list mt--40">
                                    <h4 className="price-title">Premium plans include</h4>
                                    <ul className="plan-offer-list">
                                        <li><FiCheck /> All included in Basic</li>
                                        <li><FiCheck /> Investors intros</li>
                                        <li><FiCheck /> Copywriting support</li>
                                        <li><FiCheck /> Film quality</li>
                                        <li><FiCheck /> Account manager</li>
                                        <li><FiCheck /> Custom needs</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AdvancePricingOne
