import React from "react";
import SEO from "../common/SEO";
import Separator from "../elements/separator/Separator";
import SectionTitle from "../elements/sectionTitle/SectionTitle";
import { Link } from "react-router-dom";
import HeaderOne from "../common/header/HeaderOne";
import CounterUpFour from "../elements/counterup/CounterUpFour";
import ServiceOne from "../elements/service/ServiceOne";
import AboutFour from "../elements/about/AboutFour";
import AdvancePricingOne from "../elements/advancePricing/AdvancePricingOne";
import BrandOne from "../elements/brand/BrandOne";
import FooterTwo from "../common/footer/FooterTwo";
import Copyright from "../common/footer/Copyright";
import VideoTwo from "../elements/video/VideoTwo";

const BannerData = [
  {
    image: "/images/bg/bg-image-20.jpg",
    title: "Launch videos from your Figma files",
    description: "Get yours in less than one week",
  },
];

const WebAgency = () => {
  return (
    <>
      <SEO title="Laun | Launch videos from your Figma files" />
      <main className="page-wrapper">
        <HeaderOne btnStyle="btn-small" HeaderSTyle="header-not-transparent" />

        {/* Start Slider Area  */}
        <div className="slider-area slider-style-8 height-650">
          {BannerData.map((data, index) => (
            <div key={index} className="single-slide">
              <div className="container">
                <div className="row align-items-center">
                  <div className="col-lg-12">
                    <div className="inner text-center">
                      <span className="subtitle theme-gradient">
                        Remember. You only launch once
                      </span>
                      <h1
                        className="title"
                        dangerouslySetInnerHTML={{ __html: data.title }}
                      ></h1>
                      <p
                        className="description"
                        dangerouslySetInnerHTML={{ __html: data.description }}
                      ></p>
                      <div className="button-group mt--30">
                        <a
                          className="btn-default btn-large round"
                          href="#service-area"
                        >
                          Explore
                        </a>
                        <a
                          className="btn-default btn-border btn-large round"
                          href="/contact"
                        >
                          Talk to sales
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
        {/* End Slider Area  */}

        {/* Start Counterup Area  */}
        <div className="our-counterup-area">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <div className="thumbnail">
                 <center>
                 <iframe width="90%" height="561" src="https://www.youtube.com/embed/YJ-KU34LTb4" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                 </center>
                </div>
                {/* Start Elements Area  */}
                <div className="rwt-counterup-area ptb--60">
                  <div className="col-lg-12">
                    <CounterUpFour
                      column="col-lg-3 col-md-6 col-sm-6 col-12"
                      counterStyle="counter-style-4"
                      textALign="text-center"
                    />
                  </div>
                </div>
                {/* End Elements Area  */}
              </div>
            </div>
          </div>
        </div>
        {/* End Counterup Area  */}

        <Separator />

        {/* Start Service Area  */}
        <div className="service-area rn-section-gapTop" id="service-area">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="content">
                  <h3 className="title">
                    Send your Figma files and receive your stunning launch video
                    in less than one week
                  </h3>
                </div>
              </div>
              <div className="col-lg-6">
                <p className="mb--10">
                  A dedicated account manager will guide you while building your
                  next stunning Apple-like launch video, understanding your
                  needs and leveraging our network of startups and investors to
                  help you thrive during your building journey
                </p>
                <div className="readmore-btn">
                  <Link className="btn-read-more" to="/contact">
                    <span>Get in touch</span>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* End Service Area  */}

        {/* Start Service Area  */}
        <div className="rn-service-area rn-section-gapTop">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <SectionTitle
                  textAlign="text-center"
                  radiusRounded=""
                  subtitle="How it works"
                  title="It's super simple"
                  description=""
                />
              </div>
            </div>
            <ServiceOne
              serviceStyle="service__style--1 icon-circle-style"
              textAlign="text-center"
            />
          </div>
        </div>
        {/* End Service Area  */}

        <AboutFour image="/video-thumbnail.png" />

        {/* Start Elements Area  */}
        <div className="rwt-pricingtable-area rn-section-gapBottom">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <SectionTitle
                  textAlign="text-center"
                  radiusRounded=""
                  subtitle="Our Pricing Plan"
                  title="One-time or recurring"
                  description=""
                />
              </div>
            </div>
            <div className="row mt--30">
              <div className="col-lg-8 offset-lg-2">
                <AdvancePricingOne />
              </div>
            </div>
          </div>
        </div>
        {/* End Elements Area  */}

        {/* Start Brand Area  */}
        <div className="rwt-brand-area rn-section-gapBottom">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                <SectionTitle
                  textAlign="text-center"
                  radiusRounded=""
                  subtitle="Our clients are"
                  title="Backed by the best"
                  description=""
                />
              </div>
            </div>
            <div className="row">
              <div className="col-lg-12 mt--40">
                <BrandOne brandStyle="brand-style-1" />
              </div>
            </div>
          </div>
        </div>
        {/* End Brand Area  */}

        <FooterTwo />
        <Copyright />
      </main>
    </>
  );
};
export default WebAgency;
